<template>
<div class="main">
<div class="container">
    <h1>Subnet Calculator</h1>
    <form>
      <label for="ip">Email address</label>
      <input required v-model.lazy='ip.dec' id="ip" class="form-control form-control-lg" type="text" placeholder="Ip Address/prefix">
      <label for="host">Host Number</label>
      <input required v-model.lazy='host' id="host" class="form-control form-control-lg" type="text" placeholder="Host Number">

      <button v-bind:class="{disabled: showSubnet}" @click="subnetp" type="button" class="btn btn-lg btn-dark btn-block">Subnet</button>
      <button v-bind:class="{disabled: !showSubnet}" @click="clear" type="button" class="btn btn-lg btn-dark btn-block">Reset</button>
    </form>

    <div class="card border-dark mb-3">
      <div class="card-header">Ip info</div>
      <div class="card-body text-dark">
        <h5 class="card-title">Network</h5>
        <p>Decimal: {{ip.dec}}</p>
        <p>Binary: {{binaryIP}}</p>
        <h5 class="card-title">NetMask</h5>
        <p>Decimal: {{maskIP}}</p>
        <p>Binary: {{toBinaryMask}}</p>
      </div>
    </div>
  </div>
  <subnet-app v-if="showSubnet" v-bind:ip='ip,host'></subnet-app>
</div>
</template>

<script>
  import Subnet from './Subnet.vue'
  import subetMixins from'../mixins/subnetMixins'
  export default {
    components:{
      'subnet-app': Subnet
    },
    data(){
      return{
        ip:{
          dec:'',
          prefix:'',
          ip:[]
        },
        host: '',
        showSubnet: false
      }
    },
    methods:{
      subnetp: function(){
        this.showSubnet = true;
        this.separete();
      },
      clear: function(){
        this.showSubnet = false;
        this.ip.dec = '';
        this.host = '';
      },
      separete: function(){
        var sip = this.ip.dec.split('/');
        this.ip.prefix = sip.pop();
        this.ip.ip = sip[0].split('.');
      },
      
    },
    computed:{
      binaryIP:function(){
        if(this.ip.dec != ''){
          this.separete();
          return this.ipToString(this.toBinary(this.ip.ip,true),1);
        }else{
          return ''
        }
      },
      maskIP:function(){
        if(this.ip.dec != ''){
          this.separete();
          return this.getMask(Number(this.ip.prefix)) + '/' + this.ip.prefix;
        }else{
          return ''
        }
      },
      toBinaryMask: function(){
        if(this.ip.dec != ''){
          this.separete();
          var a = this.getMask(Number(this.ip.prefix));
          a = a.split('.');
          return this.ipToString(this.toBinary(a,true),1);
        }else{
          return ''
        }
      }
    },
    mixins: [subetMixins]
  }
</script>

